"use client";

import { Breadcrumb } from "@/components/breadcrumb";
import { useEffect, useState } from "react";
import { APISDK, IDineInTableBooking } from "@/libs/api";

export default function TableReservationsPage() {
  const [bookings, setBookings] = useState<IDineInTableBooking[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedTab, setSelectedTab] = useState("today");
  const [searchQuery, setSearchQuery] = useState("");

  useEffect(() => {
    const token = localStorage.getItem("access_token");
    const fetchData = async () => {
      try {
        const api = APISDK.getInstance(token);
        const bookingsRes = await api.getBookings();

        const normalized = bookingsRes.data.map((b: any) => ({
          ...b,
          table_id: b.table_id === "undefined" ? undefined : b.table_id,
          booking_date: new Date(b.booking_date),
          from_time: new Date(b.from_time),
        }));

        setBookings(normalized);
        setIsLoading(false);
      } catch (error) {
        console.error("Error fetching bookings:", error);
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-gradient-to-br from-gray-100 to-gray-200">
        <div className="animate-spin rounded-full h-16 w-16 border-t-4 border-orange-500" />
      </div>
    );
  }

  const isToday = (date: Date) => {
    const today = new Date();
    return (
      date.getDate() === today.getDate() &&
      date.getMonth() === today.getMonth() &&
      date.getFullYear() === today.getFullYear()
    );
  };

  const isPast = (date: Date) => date < new Date() && !isToday(date);
  const isFuture = (date: Date) => date > new Date() && !isToday(date);

  const unassignedBookings = bookings.filter(
    (booking) => !booking.table_id || booking.table_id === ""
  );

  const pastBookings = unassignedBookings.filter((b) => isPast(b.booking_date));
  const todayBookings = unassignedBookings.filter((b) =>
    isToday(b.booking_date)
  );
  const futureBookings = unassignedBookings.filter((b) =>
    isFuture(b.booking_date)
  );

  const tabs = [
    { key: "today", label: "Today" },
    { key: "upcoming", label: "Upcoming" },
    { key: "past", label: "Past" },
  ];

  const filteredBookings = () => {
    let data: IDineInTableBooking[] = [];
    if (selectedTab === "today") data = todayBookings;
    else if (selectedTab === "upcoming") data = futureBookings;
    else if (selectedTab === "past") data = pastBookings;

    if (searchQuery.trim() !== "") {
      data = data.filter(
        (b) =>
          b.name?.toLowerCase().includes(searchQuery.toLowerCase()) ||
          b.phone?.includes(searchQuery)
      );
    }
    return data;
  };

  const BookingCard = ({ booking }: { booking: IDineInTableBooking }) => (
    <div
      className={`p-6 rounded-2xl shadow-md bg-white border border-gray-100 hover:shadow-lg transition duration-200 animate-fadeIn relative`}
    >
      <div className="flex justify-between items-center mb-2">
        <h4 className="text-gray-900 font-semibold text-lg tracking-tight">
          #{booking.id}
        </h4>
        <span
          className={`text-xs px-3 py-1 rounded-full font-semibold ${
            booking.is_confirmed
              ? "bg-green-100 text-green-700"
              : "bg-yellow-100 text-yellow-700"
          }`}
        >
          {booking.is_confirmed ? "Confirmed" : "Pending"}
        </span>
      </div>
      <div className="flex flex-col gap-1 text-gray-700 text-sm">
        <span>
          <span className="font-medium">Name:</span> {booking.name || "N/A"}
        </span>
        <span>
          <span className="font-medium">Phone:</span> {booking.phone || "N/A"}
        </span>
        <span>
          <span className="font-medium">Date:</span>{" "}
          {booking.booking_date.toLocaleDateString("en-GB", {
            day: "2-digit",
            month: "2-digit",
            year: "numeric",
          })}
        </span>
        <span>
          <span className="font-medium">Time:</span>{" "}
          {booking.from_time.toLocaleTimeString("en-GB", {
            hour: "2-digit",
            minute: "2-digit",
          })}
        </span>
        <span>
          <span className="font-medium">People:</span> {booking.number_of_people}
        </span>
      </div>
    </div>
  );

  return (
    <>
      <div className="sticky top-0 z-10 bg-white/80 backdrop-blur border-b border-gray-200 shadow-sm">
        <Breadcrumb items={["Cafe", "Table Reservations"]} />
        <div className="px-6 pt-2 pb-4">
          <h2 className="text-2xl md:text-3xl text-gray-900 font-bold mb-2 tracking-tight">
            Table Reservations
          </h2>
        </div>
      </div>

      <main className="flex-1 px-2 md:px-6 py-6 bg-gradient-to-br from-gray-50 to-gray-100 min-h-screen">
        {/* Tabs */}
        <div className="flex justify-center gap-2 md:gap-4 mb-8">
          {tabs.map((tab) => {
            const count =
              tab.key === "today"
                ? todayBookings.length
                : tab.key === "upcoming"
                ? futureBookings.length
                : pastBookings.length;

            return (
              <button
                key={tab.key}
                className={`relative px-5 py-2 rounded-full font-medium flex items-center gap-2 transition-all duration-200 border-2 ${
                  selectedTab === tab.key
                    ? "bg-orange-500 text-white border-orange-500 shadow"
                    : "border-gray-200 text-gray-700 bg-white hover:bg-orange-50"
                }`}
                onClick={() => setSelectedTab(tab.key)}
              >
                {tab.label}
                <span
                  className={`ml-2 text-xs font-semibold px-2 py-0.5 rounded-full ${
                    selectedTab === tab.key
                      ? "bg-white text-orange-500 border border-orange-500"
                      : "bg-orange-500 text-white"
                  }`}
                >
                  {count}
                </span>
              </button>
            );
          })}
        </div>

        {/* Search */}
        <div className="flex justify-center mb-8">
          <div className="relative w-full max-w-md">
            <span className="absolute left-3 top-2.5 text-gray-400 pointer-events-none">üîç</span>
            <input
              type="text"
              placeholder="Search by name or phone..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 pr-4 py-2 border border-gray-200 rounded-lg shadow-sm placeholder-gray-400 focus:ring-2 focus:ring-orange-400 focus:border-transparent outline-none bg-white"
            />
          </div>
        </div>

        {/* Bookings */}
        {filteredBookings().length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 max-w-5xl mx-auto">
            {filteredBookings().map((booking) => (
              <BookingCard key={booking.id} booking={booking} />
            ))}
          </div>
        ) : (
          <div className="text-center text-gray-400 text-lg mt-24 font-medium">
            No {selectedTab} bookings found.
          </div>
        )}
      </main>

      {/* Animation keyframes */}
      <style jsx global>{`
        @keyframes fadeIn {
          0% {
            opacity: 0;
            transform: translateY(10px);
          }
          100% {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .animate-fadeIn {
          animation: fadeIn 0.5s cubic-bezier(0.4, 0, 0.2, 1) forwards;
        }
      `}</style>
    </>
  );
}
